.sidebar-toggle {
  align-items: center;
  background-color: transparent;
  border: solid 1px transparent;
  display: none;
  float: right;
  height: 50px;
  justify-content: center;
  margin: 1px 1px 0;
  overflow: hidden;
  padding: 0;
  position: relative;
  text-indent: -999em;
  white-space: nowrap;
  width: 50px;
}

.sidebar-toggle:focus {
  background: #f7f7f7;
  outline: 0;
}

.sidebar-toggle:hover {
  background: transparent;
}

@media only screen and (min-width: 780px), (min-device-height: 780px) and (orientation: landscape) {
  .sidebar-toggle {
    display: flex;
  }
}

.sidebar-toggle-active .sidebar-toggle {
  border: 1px solid #bfbfbf;
  margin: 1px;
}

.sidebar-toggle, .sidebar-toggle *, .sidebar-toggle *::after, .sidebar-toggle *::before {
  box-sizing: border-box;
}

.sidebar-toggle .sidebar-toggle__icon {
  background: transparent;
  left: 0;
  margin: -2px auto auto auto;
  position: absolute;
  right: 0;
  top: 50%;
  transition: background 0.2s;
}

.sidebar-toggle .sidebar-toggle__icon, .sidebar-toggle .sidebar-toggle__icon::before, .sidebar-toggle .sidebar-toggle__icon::after {
  display: inline-block;
  height: 3px;
  position: absolute;
  width: 24px;
}

.sidebar-toggle .sidebar-toggle__icon::before, .sidebar-toggle .sidebar-toggle__icon::after {
  backface-visibility: hidden;
  background: #585858;
  content: '';
  right: 0;
  transform: translateZ(0);
  transform-origin: 0% 50%;
  transition: transform 0.2s;
}

.sidebar-toggle-active .sidebar-toggle .sidebar-toggle__icon, .sidebar-toggle-active .sidebar-toggle .sidebar-toggle__icon::before, .sidebar-toggle-active .sidebar-toggle .sidebar-toggle__icon::after {
  background: #000;
}

.sidebar-toggle .sidebar-toggle__icon::before {
  top: -6px;
  transform: translateX(4px) translateY(-3px) rotate(45deg);
}

.sidebar-toggle-active .sidebar-toggle .sidebar-toggle__icon::before {
  transform: none;
}

.sidebar-toggle .sidebar-toggle__icon::after {
  top: 6px;
  transform: translateX(4px) translateY(2px) rotate(-45deg);
}

.sidebar-toggle-active .sidebar-toggle .sidebar-toggle__icon::after {
  transform: none;
}

.sidebar-toggle:focus .sidebar-toggle__icon::before, .sidebar-toggle:focus .sidebar-toggle__icon::after, .sidebar-toggle:hover .sidebar-toggle__icon::before, .sidebar-toggle:hover .sidebar-toggle__icon::after {
  background: #000;
}

.sidebar-toggle-active .sidebar-toggle:focus .sidebar-toggle__icon, .sidebar-toggle-active .sidebar-toggle:focus .sidebar-toggle__icon::before, .sidebar-toggle-active .sidebar-toggle:focus .sidebar-toggle__icon::after, .sidebar-toggle-active .sidebar-toggle:hover .sidebar-toggle__icon, .sidebar-toggle-active .sidebar-toggle:hover .sidebar-toggle__icon::before, .sidebar-toggle-active .sidebar-toggle:hover .sidebar-toggle__icon::after {
  background: #000;
}

@media screen and (min-width: 780px), (orientation: landscape) and (min-device-height: 780px) {
  .sidebar-toggle-active .layout-region-node-main,
